body {
  background-color: #ebedef;
  background-image: linear-gradient(318deg, #ebedef 0%, #c3f8fc 100%);
  position: relative;
  min-height: 100vh; }

.main {
  width: 100%;
  max-width: 900px;
  margin: 32px auto; }
  .main__header {
    color: #0471a6;
    grid-area: header;
    margin-bottom: 48px;
    text-align: center; }

.about {
  overflow: hidden;
  grid-area: about; }
  .about__header {
    color: #0caadc;
    margin-bottom: 40px; }
  .about__img {
    width: 100%;
    max-width: 100px;
    display: block;
    float: left;
    margin: 10px 10px 20px 0; }
  .about__pharagraph {
    margin-bottom: 10px; }

.article {
  margin-top: 50px;
  padding-top: 20px; }
  .article__footer {
    margin-top: 20px;
    margin-bottom: 400px; }
  .article__header {
    color: #0471a6;
    margin-top: 75px;
    margin-bottom: 40px; }
    @media (max-width: 945px) {
      .article__header {
        color: #0471a6;
        margin-top: 0px;
        margin-bottom: 40px; } }
  .article__img {
    width: 100%;
    max-width: 600px; }
  .article__pharagraph {
    margin-bottom: 10px; }

.daily {
  margin-bottom: 45px;
  width: 100%; }
  .daily__header {
    color: #0caadc; }
  .daily__link {
    color: #70a9a1;
    text-decoration: none; }
  .daily__pharagraph {
    margin-bottom: 10px; }

.diary__header {
  color: #0471a6; }

.footer {
  display: block;
  margin-left: 10px;
  position: absolute;
  bottom: 0px;
  left: 0; }
  .footer__top {
    position: fixed;
    bottom: 10px;
    right: 10px;
    text-decoration: none;
    color: #70a9a1; }

.grid {
  display: grid;
  grid-template-areas: "header header header" "about diary diary";
  grid-template-columns: 200px 1fr 1fr;
  grid-column-gap: 32px;
  grid-row-gap: 16px; }
  .grid__diary {
    grid-area: diary; }
  .grid__daily {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 16px;
    grid-row-gap: 16px; }
    @media (max-width: 680px) {
      .grid__daily {
        display: grid;
        grid-template-columns: 1fr; } }
  @media (max-width: 945px) {
    .grid {
      display: grid;
      grid-template-areas: "header header header" "about about about" "diary diary diary";
      grid-template-columns: 1fr 1fr 1fr;
      grid-column-gap: 16px;
      grid-row-gap: 16px; } }
  @media (max-width: 680px) {
    .grid {
      display: grid;
      grid-template-columns: 1fr; } }

.navigation__item {
  margin: 0 16px; }
  @media (max-width: 945px) {
    .navigation__item {
      margin: 16px; } }

.navigation__link {
  color: #70a9a1;
  text-decoration: none; }

.navigation__list {
  list-style: none;
  padding: 0;
  display: flex;
  align-items: center;
  flex-flow: row wrap;
  justify-content: center;
  max-width: 100vh; }
  @media (max-width: 945px) {
    .navigation__list {
      flex-direction: column;
      display: none; } }
  .navigation__list--visible {
    display: flex; }

.navigation__switcher {
  border: none;
  background: none;
  position: absolute;
  right: 16px;
  top: 16px; }

.navigation__switcherX {
  border: none;
  background: none;
  position: absolute;
  right: 16px;
  top: 16px; }

.site__header {
  color: #fff;
  background-color: #fff;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  box-sizing: border-box;
  min-height: 50px;
  z-index: 10; }

@media (max-width: 945px) {
  .navigation__list--visible {
    display: flex; } }

@media (min-width: 946px) {
  .navigation__switcher {
    display: none; } }
